* {
    font-family: 'Courier New', Courier, monospace;
}

body {
    padding: 60px;
}

input {
    width: 100%;
    color: gray;
}

.card-grid {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    size: 100px;
}

.card {
    align-content: center;
    border: 1px solid gray;
    border-radius: 6px;
    margin-bottom: 10px;
    margin-right: 10px;
    overflow: hidden;
    size: 100px;
}

.card p {
    padding: 5px;
    text-align: center;
}

.card img {
    object-fit: cover;
    size: 100px;
}